import React from "react";
import { PortFolioAddWrap } from "../../../styles/AddPofolModalStyle";

const AddPofolModal = ({
  modalOpen,
  handleAddModalClose,
  fileType,
  setFileType,
  selectFile,
  setSelectFile,
  linkUrl,
  setLinkUrl,
  fileOneWord,
  setFileOneWord,
  linkOneWord,
  setLinkOneWord,
  handleFileUpload,
}) => {
  const handleDescriptionChange = e => {
    const inputText = e.target.value;

    const limitedText = inputText.slice(0, 150);
    if (fileType === 2) {
      setFileOneWord(limitedText);
    } else if (fileType === 3) {
      setLinkOneWord(limitedText);
    }
  };

  const handleSelectFileChange = e => {
    const file = e.target.files[0];

    if (file) {
      setSelectFile(file);
    }
  };
  return (
    <PortFolioAddWrap>
      {modalOpen && (
        <div className="dim">
          <div className="add-modal-inner">
            <div className="add-modal-top">
              <div>
                <h2> </h2>
              </div>
              <div>
                <span onClick={handleAddModalClose}></span>
              </div>
            </div>
            <ul className="add-modal-btm">
              <li className="radio-box">
                <input
                  type="radio"
                  id="fileTypeFile"
                  name="fileType"
                  value={2}
                  checked={fileType === 2}
                  onChange={() => {
                    setFileType(2);
                  }}
                />
                <label htmlFor="fileTypeFile"></label>
                <input
                  type="radio"
                  id="fileTypeLink"
                  name="fileType"
                  value={3}
                  checked={fileType === 3}
                  onChange={() => setFileType(3)}
                />
                <label htmlFor="fileTypeLink"></label>
              </li>
              {fileType === 2 ? (
                <>
                  <li className="file-box">
                    <input
                      type="file"
                      id="file"
                      accept=".pdf"
                      onChange={handleSelectFileChange}
                    />
                    <label htmlFor="file"></label>
                    <input
                      className="upload-name"
                      value={selectFile ? selectFile.name : ""}
                      placeholder="PDF "
                      readOnly
                    />
                  </li>
                  <li>
                    <textarea
                      cols="30"
                      rows="5"
                      placeholder="   .( 150)"
                      value={fileOneWord}
                      onChange={handleDescriptionChange}
                    />
                  </li>
                  <li>
                    <span>
                      *   5  , 
                      1   .
                    </span>
                    <span>*   50MB  .</span>
                  </li>
                </>
              ) : fileType === 3 ? (
                <>
                  <li className="file-box">
                    <input
                      className="upload-link"
                      placeholder="  ."
                      value={linkUrl}
                      onChange={e => setLinkUrl(e.target.value)}
                    />
                  </li>
                  <li>
                    <textarea
                      cols="30"
                      rows="5"
                      placeholder="   .( 150)"
                      value={linkOneWord}
                      onChange={handleDescriptionChange}
                    />
                  </li>
                  <li>
                    <span>
                      *   5  , 
                      1   .
                    </span>
                  </li>
                </>
              ) : null}
              <li>
                <button onClick={handleFileUpload}></button>
              </li>
            </ul>
          </div>
        </div>
      )}
    </PortFolioAddWrap>
  );
};

export default AddPofolModal;
