import { AcceptModalWrap, CancelModalWrap } from "../styles/AcceptModalStyle";

export const AcceptModal = ({ acceptOkModal, uploadResult, handleOk }) => {
  return (
    <>
      {acceptOkModal && (
        <AcceptModalWrap>
          <div className="dim">
            <div className="content-wrap">
              <div className="header">
                <span></span>
              </div>
              <div className="content">
                <span>
                  {uploadResult
                    ? " ."
                    : "  :  ."}
                </span>
              </div>
              <div className="btns">
                <button onClick={handleOk}></button>
              </div>
            </div>
          </div>
        </AcceptModalWrap>
      )}
    </>
  );
};

export const MainYnModal = ({
  mainYnModal,
  handleMainPofolOk,
  handleMainCancel,
  mainYn,
}) => {
  return (
    <>
      {mainYnModal && (
        <AcceptModalWrap>
          <div className="dim">
            <div className="content-wrap">
              <div className="header">
                <span></span>
              </div>
              {mainYn === 0 ? (
                <div className="content">
                  <span>  ?</span>
                </div>
              ) : (
                <div className="content">
                  <span>   ?</span>
                </div>
              )}

              <div className="btns">
                <button onClick={handleMainPofolOk}></button>
                <button onClick={handleMainCancel}></button>
              </div>
            </div>
          </div>
        </AcceptModalWrap>
      )}
    </>
  );
};

export const DeleteModal = ({
  deleteOkModal,
  handleDeleteOk,
  handleDeleteCancel,
}) => {
  return (
    <>
      {deleteOkModal && (
        <CancelModalWrap>
          <div className="dim">
            <div className="content-wrap">
              <div className="header">
                <span onClick={handleDeleteCancel}></span>
              </div>
              <div className="content">
                <span>   ?</span>
              </div>
              <div className="btns">
                <button onClick={handleDeleteCancel}></button>
                <button onClick={handleDeleteOk}></button>
              </div>
            </div>
          </div>
        </CancelModalWrap>
      )}
    </>
  );
};
