import axios from "axios";
import { getCookie, removeCookie, setCookie } from "./cookie";
import { useRecoilState } from "recoil";
import { AgreeModalAtom } from "../pages/businessPages/Business";

// axios  
export const client = axios.create({
  baseURL: "/api",
  headers: {
    "Content-Type": "application/json",
  },
});

//   
client.interceptors.request.use(
  async config => {
    const token = getCookie("accessToken");
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }
    return config;
  },
  error => {
    console.log(error);
    return Promise.reject(error);
  },
);

//   
client.interceptors.response.use(
  response => {
    return response;
  },
  async error => {
    const { config, response } = error;
    const refreshToken = getCookie("refreshToken");
    if (response && response.status === 401 && refreshToken) {
      try {
        const { data } = await client.post(`/company/refresh-token`, {
          refreshToken,
        });

        const accessToken = data;
        setCookie("accessToken", accessToken);

        if (config && config.headers && config.headers.Authorization) {
          config.headers.Authorization = `Bearer ${accessToken}`;
          const retryResponse = await client(config);
          return retryResponse;
        }
      } catch (error) {
        console.log("  :", error);
      }
    }
    console.error(" :", error);
    return Promise.reject(error);
  },
);

//  
export const fetchLogin = async (userId, password, setErrorCancelInfo) => {
  try {
    const res = await client.post(`/sign/sign-in`, {
      email: userId,
      pw: password,
    });

    const { role, refreshToken, accessToken, vo } = await res.data;

    if (role && refreshToken && accessToken) {
      const cookieOptions = {
        path: "/",
        secure: true,
        sameSite: "none",
        httpOnly: true,
      };

      setCookie("refreshToken", refreshToken, cookieOptions);
      setCookie("accessToken", accessToken, cookieOptions);
      setErrorCancelInfo("");
      return { role, accessToken, vo };
    } else {
      throw new Error("  ");
    }
  } catch (error) {
    if (error.response.status === 432) {
      setErrorCancelInfo("    ");
    }
    if (error.response.status === 434) {
      setErrorCancelInfo("    ");
    }
    if (error.response.status === 435) {
      setErrorCancelInfo(" .   .");
    }
    if (error.response.status === 500) {
      setErrorCancelInfo("  .");
    }
    throw new Error(" .");
  }
};

//  
export const postLogout = async (accessToken, refreshToken) => {
  try {
    const res = await client.post("/sign/logout");
    removeCookie(accessToken);
    removeCookie(refreshToken);
  } catch (error) {
    console.log(error);
  }
};
